@import "../../resources/variables.scss";
.first-section {
  display: flex;
  flex-flow: column;
  justify-content: center;
  .name {
    font-size: 5rem;
    color: $color-name;
  }
  .skills {
    font-size: 1.2rem;
    color: $color-name;
  }
  background: linear-gradient(-45deg, #002f4f, #002f4f, #a0895d, #fadc17);
  background-size: 400% 400%;
  animation: gradient 25s ease infinite;
  @keyframes gradient {
    0% {
      background-position: 0% 50%;
      //border-radius:0% 0%  70% 70%;
    }
    50% {
      background-position: 100% 50%;
      //border-radius: 0% 0%  0% 0%;
    }
    100% {
      background-position: 0% 50%;
      // border-radius: 0% 0%  70% 70%;
    }
  }
}
.short-info-box {
  display: flex;
  flex-flow: row;
  justify-content: center;

  .short-info {
    text-align: left;
    display: flex;
    flex-flow: column;
    justify-content: center;

    p {
      padding: 10px;
      width: 500px;
    }
  }
  .image-container {
    margin: 80px 0;
    display: grid;
    position: relative; // imperative for the overlapping to work
    grid-template-columns: repeat(8, 1fr);
    .bot-image {
      grid-column: 4 / -1;
      grid-row: 1;
      img {
        width: 280px;
        height: 400px;
        object-fit: cover;
        box-shadow: $box-shadow;
      }
    }
    .top-image {
      grid-row: 1;
      grid-column: 1 / span 5;
      padding-top: 20%; // slightly arbitrary, keeps proportion once resized
      z-index: 1; // tells the browser to make this image on top
      img {
        width: 270px;
        height: 300px;

        object-fit: cover;
        box-shadow: $box-shadow;
      }
    }
  }
}
