.navigation{position:fixed;top:0;left:0;right:0;padding:0;display:flex;flex-flow:column wrap;align-items:center;justify-content:center;box-sizing:border-box;height:80px;background-color:#1b1b1e;z-index:10000;opacity:90%}.navigation .logo{display:flex;flex-flow:row}.navigation .logo img{height:55px}.navigation .logo .text{color:#fbfbfc;display:flex;flex-flow:column;justify-content:center;align-items:flex-start}.navigation .logo .text h4{margin:2px}.navigation .menu a{display:inline-block;line-height:80px;padding:0 10px;font-size:1rem;color:#fbfbfc}@media only screen and (max-width: 640px){.navigation .menu a{padding:0 5px}}.navigation .menu a:hover{color:#e27272}.navigation .menu .active{color:#e27272;font-weight:bold;-webkit-text-decoration:underline #e27272;text-decoration:underline #e27272}@media only screen and (max-width: 640px){.navigation .icons{display:none}}.navigation .icons a{margin:0 5px;color:#fbfbfc;display:inline-block}.navigation .icons a:hover{color:#e27272}

.first-section{display:flex;flex-flow:column;justify-content:center;background:linear-gradient(-45deg, #002f4f, #002f4f, #a0895d, #fadc17);background-size:400% 400%;-webkit-animation:gradient 25s ease infinite;animation:gradient 25s ease infinite}.first-section .name{font-size:5rem;color:#fbfbfc}.first-section .skills{font-size:1.2rem;color:#fbfbfc}@-webkit-keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}.short-info-box{display:flex;flex-flow:row;justify-content:center}.short-info-box .short-info{text-align:left;display:flex;flex-flow:column;justify-content:center}.short-info-box .short-info p{padding:10px;width:500px}.short-info-box .image-container{margin:80px 0;display:grid;position:relative;grid-template-columns:repeat(8, 1fr)}.short-info-box .image-container .bot-image{grid-column:4 / -1;grid-row:1}.short-info-box .image-container .bot-image img{width:280px;height:400px;object-fit:cover;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.short-info-box .image-container .top-image{grid-row:1;grid-column:1 / span 5;padding-top:20%;z-index:1}.short-info-box .image-container .top-image img{width:270px;height:300px;object-fit:cover;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}

.about{margin:0;padding:0}.about .header{margin:80px 80px;display:flex;flex-flow:row wrap;justify-content:center;padding:80px}.about .header .text{margin:0 20px}.about .header .text h2{text-align:left}.about .header .text p{padding:10px;width:460px;text-align:left}.about .header img{height:400px;object-fit:cover}

.footer{min-height:20vh;background:#1b1b1e;padding:80px 80px 10px 80px;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr 50px;grid-template-areas:"com com" "copy copy";grid-gap:10px}.footer .box{display:flex;align-items:center;flex-flow:column;width:100%}.footer .box div{display:flex;align-items:center;flex-flow:column}.footer .box div h2{display:block;color:#fbfbfc;margin-bottom:5px}.footer .box div p{font-size:0.9rem;color:#fbfbfc}.footer .box div a{font-size:0.9rem;color:#fbfbfc}.footer .box div a:hover{color:#e27272}.footer .copyright{grid-area:copy;display:flex;align-items:center;justify-content:center;color:#fbfbfc;font-size:0.9rem;font-family:"Montserrat", sans-serif;opacity:0.7}

.projects .header{margin:40px}.projects .projectBoxes{display:flex;flex-flow:row wrap;justify-content:space-evenly}

.project-box{display:flex;flex-direction:column;background-color:#e5e2e2;height:300px;width:300px;justify-content:center;border-radius:20px;margin:20px 0;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.project-box h2{color:#1b1b1e}.project-box img{max-width:85%;height:auto;margin:0 auto}.project-box:hover{background-color:#1b1b1e}.project-box:hover h2{color:#e5e2e2}@media only screen and (max-width: 640px){.project-box{margin:5px 0}}

.project-site .top-ps{margin:80px 0px;height:1000px}.project-site .top-ps .title{height:40%;width:100%}.project-site .top-ps .title .background{height:100%;width:100%;object-fit:cover;display:block;-webkit-filter:blur(5px);filter:blur(5px);z-index:-1}.project-site .top-ps .title .logo{position:absolute;top:220px;left:50%;transform:translate(-50%, -50%);height:25%;width:auto;background-color:none;z-index:2}.project-site .top-ps .title h1{position:absolute;top:370px;left:50%;transform:translate(-50%, -50%);font-size:5vh;color:#fbfbfc;z-index:2}.project-site .top-ps .title h3{position:absolute;top:410px;left:50%;font-size:2vh;transform:translate(-50%, -50%);color:#fbfbfc;z-index:2}.project-site .top-ps .content{display:flex;flex-direction:column;margin:40px 80px;align-items:center;justify-content:center}.project-site .top-ps .content .about{display:flex;flex-flow:row}.project-site .top-ps .content .about img{width:500px;margin:40px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);border-radius:5px}.project-site .top-ps .content .about .about-text{text-align:left;width:500px;padding:10px;margin:40px}.project-site .top-ps .content .link-button{margin:5px;background-color:#fbfbfc;border-radius:3px;border:solid #1b1b1e;text-align:center;width:150px;padding:5px;margin:5px}.project-site .top-ps .content .link-button:hover{background-color:#1b1b1e}.project-site .top-ps .content .link-button:hover p{color:#fbfbfc}.project-site .second-part .image-sp{background-color:#e5e2e2;margin:0 0 10px 0;padding:40px 0;display:flex;flex-flow:row;justify-content:center}.project-site .second-part .image-sp img{border-radius:5px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);height:350px}.project-site .second-part .image-sp .image-sp-text{width:450px;padding:10px}.project-site .second-part .image-sp-2{margin:0 0 10px 0;padding:40px 0;display:flex;flex-flow:row;justify-content:center}.project-site .second-part .image-sp-2 img{height:350px;border-radius:5px}.project-site .second-part .image-sp-2 .image-sp-text-2{width:450px;padding:10px}

.program-used .programs{display:flex;flex-flow:row}.program-used .programs p{font-size:35px}

.resume{background-color:white;padding:0}.resume .top{padding:30px;background-image:url("/images/copenhagen2.jpg");background-size:cover}.resume .top img{margin-top:80px;width:300px;height:300px;object-fit:cover;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);border-radius:50%}.resume .top h1{color:white;margin:20px;float:center}.resume .top h4{color:white;margin-bottom:80px}.resume .downloadRes{margin:4px 0;padding:50px;display:flex;flex-flow:row;justify-content:center}.resume .downloadRes .download-button{background-color:#fbfbfc;border-radius:3px;text-align:center;width:150px;padding:5px;margin:5px;border:3px solid #1b1b1e}.resume .downloadRes .download-button p{color:#1b1b1e}.resume .downloadRes .download-button:hover{border:3px solid #1b1b1e;background-color:#1b1b1e}.resume .downloadRes .download-button:hover p{color:#fbfbfc}

.programing-skills{padding:80px}.programing-skills .skills{margin:40px;justify-content:center;display:flex;flex-flow:row}.programing-skills .skills .type{width:130px}

.design-skills{padding:80px;background-color:#e5e2e2}.design-skills .design{display:flex;flex-flow:row;margin:40px;justify-content:center}.design-skills .design .design-type{width:130px}

.work{padding:80px}.work .work-box{display:flex;flex-flow:row;justify-content:space-between;margin:40px 20%;text-align:left}.work .work-box .right{text-align:right}

.education{background-color:#e5e2e2;padding:80px}.education .edu-box{display:flex;flex-flow:row;justify-content:space-between;margin:20px 20%;text-align:left}.education .edu-box .right{text-align:right}

.app{text-align:center}

