@import "../../resources/variables.scss";
.about {
  margin: 0;
  padding: 0;
  .header {
    margin: 80px 80px;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    padding: 80px;

    .text {
      margin:0 20px ;
      h2{
        text-align: left;
      }
      p {
        padding: 10px;
        width: 460px;
        text-align: left;
      }
    }
    img {
      height: 400px;
      object-fit: cover;
     
    }
  }
}
